
body {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  background-color: #111;
  min-height: 100vh;
  color: #fff;
  font-family: 'Bandeins Strange Variable';
  overflow: hidden;
  text-align: center;
  white-space: nowrap;
  flex-direction: column;
}

h1 {
  font-size: 10vw;
  font-variation-settings: 'wght' 0;
  margin: 0;
  line-height: 1;
}
